.ladakh_main_container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
}


.ladakh_card_container {
    display: flex;
    flex-direction: row;



}

.content_container {
    margin: 25px;

}

.ladakh_header {
    font-size: 35px;
    font-family: "Roboto";
    font-weight: bold;
    color: #000000;
    padding: 10px;
}

.ladakh_description {
    font-size: 18px;
    font-family: "Roboto";
    font-weight: 400;
    color: #000000;
    padding: 10px;
}

.facebook_icon {
    padding: 10px;
    font-size: 35px;
}

.instagram_icon {
    padding: 10px;
    font-size: 35px;
}

.ladakh_image {
    height: 500px;
    width: 100%;
}

.checklist_container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    width: 48%;
    position: absolute;
    bottom: 4px;
    border: 1px solid;
    padding: 15px;
    background-color: #ffffff;
    border-radius: 10px;
}

.book_btn {
    background-color: rgb(106, 35, 172);
    color: #ffffff;
    font-size: 20px;
    font-weight: bold;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 12px;
    padding-bottom: 12px;
    border-radius: 5px;
}