.Nav_container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    margin: 10px;
}

.ul_container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}

.ul_container li {
    list-style: none;
    padding: 10px;
    margin: 10px;
    font-size: 16px;
    font-family: "Roboto";
    color: #000000;
    font-weight: bold;
}

.Brisphere_header {
    font-size: 20px;
    font-family: "Roboto";
    color: #000000;
    font-weight: bold;
    padding-left: 15px;
}