.main_service_card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.services_heading {
    font-size: 30px;
    font-family: 'roboto';
    font-weight: bold;
    color: #000000;
    padding: 10px;
}

.service_container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    width: 1200px;
}


.wifi {
    height: 70px;
    margin: 10px;
}

.service_card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 300px;
    border: 2px solid aliceblue;
    padding: 10px;
    margin: 10px;
    background-color: #ffffff;
    border-radius: 5px;
}

.heading {
    font-size: 20px;
    font-family: 'roboto';
    font-weight: bold;
}

.service_content {
    font-size: 15px;
    font-family: 'roboto';
    font-weight: 300;
}