.profile_icon {
    height: 80px;
    width: 80px;
    border-radius: 50px;
    margin: 10px;
}

.stars {
    height: 20px;
    width: 80px;
}

.profile_container {
    display: flex;
    flex-direction: row;
    align-items: center;

}

.profile_card {
    border: 1px solid;
    border-radius: 10px;
    width: 300px;
    padding: 12px;
    margin: 10px;
}

.profile_description {
    font-size: 16px;
    font-family: 'roboto';
    font-weight: 400;
}

.river_img {
    height: 500px;
    width: 350px;
}

.Discover_container {
    display: flex;
    flex-direction: row;
    justify-content: center;

}

.discover_heading {
    font-size: 30px;
    font-family: 'roboto';
    font-weight: bold;
    color: #000000;
    text-align: center;
    padding: 10px;
}