.booking_container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    border: 1px solid #d3d3d3;
    margin: auto;
    padding: 10px;
    width: 70%;
    position: relative;
    height: 300px;
    flex-wrap: wrap;
    background-color: #d3d3d3;
}


.details_container {
    height: 30px;
    padding: 20px;
    margin: 20px;
    background-color: #ffffff;
    border-radius: 8px;
}



.orderchecklist_container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    width: 48%;
    position: relative;
    bottom: 40px;
    border: 1px solid;
    padding: 15px;
    background-color: #ffffff;
    border-radius: 10px;
    margin: auto;
}

.amount_container {
    height: 30px;
    width: 150px;
    background-color: blueviolet;
    color: #ffffff;
    padding: 20px;
    font-size: 20px;
    font-weight: bold;
    border-radius: 5px;
}

.amountbtn_container {
    height: 68px;
    padding: 6px;
    font-size: 20px;
    background-color: blueviolet;
    color: #ffffff;
    font-weight: bold;
    border-radius: 5px;
}

.amount_btn {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.checkin {
    border-right: 1px solid #000;
    padding-right: 20px;
}

.checkout {
    border-right: 1px solid #000;
    padding-right: 20px;
}

.no_rooms {
    text-align: center;
    padding: 10px;
}